body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
    background: #f0f5f9;
    font-size: 1.2rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.container {
    margin-top: 4rem;
}

.col {
    margin: 0 auto;
    max-width: 500px;
    border: 1px solid gray;
    box-shadow: 3px 5px 10px 1px rgba(0, 0, 0, 0.3);
    position: relative;
    
}

del {
    color: #555;
    text-decoration: none;
    position: relative;
    display: inline-block;
}

del:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 3px solid rgba(255, 0, 0, 0.4);
  height: 45%;
  height: calc(50% - 1px);
  width: 100%;
  -webkit-transform: rotateZ(-4deg);
          transform: rotateZ(-4deg);
}

.p1 {
    position: absolute;
    z-index: -1;
    bottom: -5px;
    left: 6px;
    max-width: 488px;
}
.p2 {
    position: absolute;
    z-index: -2;
    bottom: -9px;
    left: 11px;
    max-width: 478px;
}
.success {
    color: #155724;
    background-color: #d4edda;
}
.info {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 1rem;
    color: gray;
}

.info > a {
    color: gray;
}

.info > a:hover {
    color: #0056b3;
    text-decoration: none;
}


.btn-group {
    padding: 5px 0;
    margin-bottom: 5px;
    -webkit-align-items: baseline;
            align-items: baseline;
}



